import { defineComponent, computed, ref } from 'vue';
import { useSystemStore } from '@/stores/modules/system';
import XtTable from '@/base-ui/table';
import { useStore } from 'vuex';
export default defineComponent({
  name: 'PageContent',
  components: {
    XtTable
  },
  props: {
    contentTableConf: {
      type: Object,
      required: true
    },
    pageName: {
      type: String,
      required: true
    }
  },
  emits: ['newBtnClick', 'editBtnClick'],

  setup(props, {
    emit
  }) {
    const systemStore = useSystemStore(); //TODO:Vuex4piniahooks

    const store = useStore();

    const usePermission = (pageName, handleName) => {
      const permissions = store.state.login.permissions;
      const verifyPermission = `system:${pageName}:${handleName}`; //!! 

      return !!permissions.find(item => item === verifyPermission);
    }; //
    // 


    const isCreate = usePermission(props.pageName, 'create');
    const isUpdate = usePermission(props.pageName, 'update');
    const isDelete = usePermission(props.pageName, 'delete');
    const isQuery = usePermission(props.pageName, 'query'); // pageInfo

    const pageInfo = ref({
      currentPage: 1,
      pageSize: 10
    }); //TODO:BUG2010
    // watch(pageInfo, () => getPageData());
    //

    const getPageData = (info = {}) => {
      //
      if (!isQuery) return;
      systemStore.getPageListsAction({
        pageName: props.pageName,
        queryInfo: {
          offset: (pageInfo.value.currentPage - 1) * pageInfo.value.pageSize,
          size: pageInfo.value.pageSize,
          ...info
        }
      });
    };

    getPageData(); //Getter

    const dataList = computed(() => {
      return systemStore.pageListData(props.pageName);
    });
    const dataCount = computed(() => {
      return systemStore.pageCountData(props.pageName);
    }); // 

    const otherPropSlots = props.contentTableConf?.propList.filter(item => {
      if (item.slotName === 'status') return false;
      if (item.slotName === 'createAt') return false;
      if (item.slotName === 'updateAt') return false;
      if (item.slotName === 'handler') return false;
      return true;
    }); //
    //

    const handleNewClick = () => {
      emit('newBtnClick');
    };

    const handleEditClick = item => {
      emit('editBtnClick', item);
    }; //


    const handleDeleteClick = item => {
      systemStore.deletePageDataAction({
        pageName: props.pageName,
        id: item.id
      });
    };

    return {
      getPageData,
      dataList,
      dataCount,
      pageInfo,
      otherPropSlots,
      isCreate,
      isUpdate,
      isDelete,
      handleDeleteClick,
      handleNewClick,
      handleEditClick
    };
  }

});